.about{
  background-color: var(--gray-color);

  &__container{
    padding-top: 136px;
    display: flex;
    gap: 30px;
    padding-bottom: 493px;
  }
  &__left{
    background-image: url('../img/about_main.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 445px;
    height: 672px;
  }
  &__right{
    position: relative;
    max-width: 635px;
  }
  &__decor{
    position: absolute;
    top: 387px;
    right: 0px;
    width: 337px;
    height: 738px;
    background-image: url('../img/about_bg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &__title{
    margin: 0;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 36px;
    line-height: 104.52%;
    text-align: justify;
    margin-bottom: 50px;
  }
  &__subtitle{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
    margin-bottom: 50px;

    &::before{
      content: '';
      width: 70px;
      height: 2px;
      background-color: var(--text-color);
      margin-right: 10px;
    }
  }
  &__list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 50px;
  }
  &__item{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
  }
  &__item-img{
    border-radius: 100%;
    width: 70px;
    height: 70px;
  }
  &__descr{
    margin: 0;
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
  }
}
@include big-desktop{
  .about__container{
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    padding-bottom: 0;
  }
  .about__decor{
    top: 40px;
    right: -15px;
  }
}
@include small-tablet{
  .about__list{
    display: flex;
    flex-direction: column;
    align-items: start;
  }
}
@include mobile{
  .about__title{
    font-size: 26px;
  }
  .about__left{
    border-radius: 20px;
    width: 300px;
    height: 472px;
  }
  .about__decor{
    top: 120px;
   right: -130px;
  }
}
