.card{

  background-color: var(--gray-color);

  &__title{
    margin: 0;
    padding-top: 100px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
  }
  &__list{
    margin-top: 160px;
    max-width: 1050px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 30px;
    row-gap: 50px;
  }
  &__item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 540px;
    height: 650px;
    background: var(--light-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }
  &__img{
    border-radius: 10px;
    width: 500px;
    height: 445px;
    padding-bottom: 36px;
  }
  &__text{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 117.52%;
    text-align: center;
    padding-bottom: 36px;
  }
  &__link{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: var(--light-color);
  }
}
@include big-desktop{
  .card__list{
    grid-template-columns: repeat(1, 1fr);
    gap:30px;
    justify-items: center;
    margin-top: 40px;
  }
}
@include tablet{
  .card__title{
    font-size: 24px;
  }
}
@include mobile{
  .card__title{
    font-size: 20px;
  }
  .card__item{
    width: 300px;
    height: 450px;
  }
  .card__img{
    width: 260px;
    height: 245px;
  }
}
