.offer{
  background-color:#F5F5F5;

  &__title{
    margin: 0 auto;
    text-align: center;
    padding: 139px 20px 31px 20px;
    max-width: 900px;
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
  }
  &__link{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: var(--light-color);
  }
  &__btn{
    display: block;
    margin: 0 auto;
  }
  &__inner{
    margin-top: 131px;
    display: flex;
    justify-content: center;
    gap: 32px;
  }
  &__right{
    padding: 27px 40px;
    width: 350px;
    height: 628px;
    background: #282728;
    border-radius: 20px;
    color: var(--light-color);
  }
  &__descr{
    margin: 0;
    padding: 0px 20px;
    text-align: center;
    text-transform: uppercase;
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
  }
  &__right-img{
    width: 269px;
    height: 242px;
    margin-bottom: 16px;
  }
  &__text{
    margin: 0;
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    margin-bottom: 16px;
  }
  &__right-inner{
    display: flex;
    align-items: center;
    gap: 38px;
    margin-bottom: 26px;
  }
  &__inner-img{
    width: 80px;
    height: 111px;
  }
  &__text-inner{
    margin: 0;
    max-width: 131px;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    text-align: center;
  }
  &__btn{
    padding-top: 10px;
    color: var(--light-color);
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 8px;
    line-height: 12px;
    text-align: center;
  }
  &__tel{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    font-family: 'TT Norms';
    color: var(--light-color);
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;

    &::before{
      content: url('../img/svg/phone.svg');
      width: 27px;
      height: 27px;
    }
  }
}

@include tablet{
  .offer__title{
    padding: 0;
    font-size: 16px !important;
    line-height: 30px;
    font-weight: 400;
  }

  .offer__inner{
    margin-top: 20px;
  }
  .offer__title{
    font-size: 40px;
    padding: 39px 0px 31px 0px;
  }
}
@include small-tablet{
  .offer__inner{
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }
}
@include mobile{
  .offer__right{
    padding: 5px;
    width: 280px !important;
    height: 628px;
  }
}
