.hero{
  background-color: var(--gray-color);
  color: var(--text-color);

  &__container{
    background-image: url('../img/hero_bg.png');
    background-repeat: no-repeat;
    background-position: top 40px right;
  }

  &__title{
    margin: 0;
    padding-top: 140px;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 20px;
    line-height: 117.52%;
    color: #282828;

    span{
    display: block;
    padding: 24px 0px 24px 0px;
    max-width: 615px;
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 48px;
    line-height: 50px;
    }
  }
  &__subtitle{
    margin: 0;
    max-width: 457px;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    margin-bottom: 16px;
  }
  &__list{
    max-width: 600px;
    margin-bottom: 31px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  &__item{
    position: relative;
    max-width: 286px;

    &::before{
      position: absolute;
      top: 10px;
      left: 0;
      content: url('../img/svg/check.svg');
    }
  }
  &__item-title{
    margin: 0;
    padding-left: 44px;
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 13px;
    line-height: 15px;
  }
  &__text{
    display: block;
    padding-left: 44px;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
  }
}

@include tablet{
  .hero__title{
    padding-bottom: 120px;
    max-width: 270px;
    color: #282828 ;

    span{
      color: #3FAC3B;
      font-size: 38px;
    }
  }
}
@include mobile{
  .hero__list{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .btn__hero{
    display: block;
    margin: 0 auto;
  }
}
