.snowgarden{

  background-color: var(--gray-color);
  min-height: 100vh;

  &__title{
    margin: 0;
    max-width: 420px;
    padding: 0px 0px 28px 80px;
    padding-top: 126px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
  }
  &__inner{
    display: flex;
  }
  &__left{
    max-width: 460px;
    padding-bottom: 50px;
  }
  &__img{
    border-radius: 15px;

    &-1{
      margin-left: 15px;
      width: 401px;
      height: 268px;
      margin-bottom: 28px;
    }
    &-2{
      width: 214px;
      height: 100px;

      &:not(:last-child){
        margin-right: 28px;
      }
    }
  }
  &__right{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 32px;
    padding-left: 48px;
    border-left: 1px solid #282828;
  }
  &__descr{
    margin: 0;
    max-width: 565px;
    margin-bottom: 32px;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 32px;
    }
    &-white{
      border-radius: 50px;
      padding: 10px 40px;
      border: 2px solid #3FAC3B;
      max-width: 257px;
      height: 58px;
    }
  }
  &__link{
    padding: 15px 20px;
    color: var(--light-color);
  }
  &__link-green{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    font-size: 13px;
    line-height: 16px;
    text-transform: uppercase;
    color: #3FAC3B;
  }
}
@include big-desktop{

  .snowgarden__title{
    max-width: none;
    padding: 120px 0px 28px 0px;
    text-align: center;
  }
  .snowgarden__inner{
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  .snowgarden__left{
    padding-bottom: 0;
  }
  .snowgarden__right{
    border-left: none;
    margin-left: 0;
    padding-left: 0;
  }
  .snowgarden__descr{
    text-align: center;
    max-width: none;
  }
  .snowgarden__img{
    &-1{
      margin-left: 15px;
      width: 401px;
      height: 268px;
      margin-bottom: 10px;
    }
    &-2{
      width: 214px;
      height: 100px;

      &:not(:last-child){
        margin-right: 10px;
      }
    }
  }
}

@include mobile{
  .snowgarden{
    padding-bottom: 30px;
  }
  .snowgarden__img{
    display: flex;
    flex-direction: column;
    align-items: center;

    &-1{
      margin-left: 0px;
      width: 280px;
      height: 150px;
      margin-bottom: 20px;
    }
    &-2{
      width: 280px;
      height: 150px;
      margin-bottom: 20px;
    }
  }
  .snowgarden__buttns-box{
    display: flex;
    gap: 20px;
    flex-direction: column;
    align-items: center;
  }
  .snowgarden__btn{
    margin: 0 !important;
    min-width: 300px;
  }
  .snowgarden__link{
    padding: 20px 60px;
  }
  .snowgarden__link-green{
   padding: 20px 20px 20px 70px;
  }
}
