// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "TTNorms", "Inter", "Open Sans", sans-serif;
  --content-width: 1142px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --gray-color:#F5F5F5;
  --text-color:#282828;
  --green-color:#3FAC3B;
  --light-grey-color:#969696;
}
