.footer-garden{
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 0;
  padding-top: 100px;

  &__container{
    max-width: 1111px;
    background: #555555;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__text{
    margin: 0;
    max-width: 136px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 119.52%;
    color: var(--light-color);
  }
  &__copyright{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 119.52%;
    color: var(--light-color);
  }
  &__link{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 119.52%;
    text-decoration-line: underline;
    color: var(--light-color);
  }
}
@include tablet{
  .footer-garden{
    position: static;
    padding-top: 0;
  }
  .footer-garden__container{
    padding-top: 20px;
    flex-direction: column;
    align-items: center;
  }
  .footer-garden__text{
    text-align: center;
  }
}

