.contacts{
  background-color: var(--gray-color);
  padding-top: 100px;

  &__container{
    display: flex;
    gap: 26px;
  }
  &__title{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 119.52%;
    text-align: center;
  }
  &__subtitle{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 25px;
    line-height: 119.52%;
    text-align: center;
    margin-bottom: 32px;
  }
  &__left{
    padding: 76px 30px;
    background-image: url('../img/form (1).png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    max-width: 443px;
    height: 422px;
  }
  &__input{
    outline: none;
    padding-left: 90px;
    width: 332px;
    height: 54px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    margin-bottom: 32px;
    &::placeholder{
      font-family: 'TT Norms';
      font-style: normal;
      font-weight: 700;
      font-size: 15px;
      line-height: 119.52%;
      color: #E2E2E2;
    }
  }
  &__btn{
    width: 332px;
    height: 62px;
  }
  &__link{
    width: 332px;
    color: var(--light-color);
  }
  &__map{
    border-radius: 20px;
  }
  &__text{
    margin: 0;
    max-width: 220px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
  }
  &__descr{
    margin: 0;
    padding: 24px 0px ;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;

    &::before{
      content: '';
      display: inline-block;
      width: 3px;
      height: 3px;
      margin-bottom: 3px;
      margin-right: 5px;
      background-color: #00AF47;
    }
  }
  &__phone{
    display: block;
      font-family: 'TT Norms';
      font-style: normal;
      font-weight: 700;
      font-size: 25px;
      line-height: 30px;
      color: #282828;
      margin-bottom: 24px;
  }
  &__email{
    display: block;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 24px;
  }
  &__sotial{
    margin-bottom: 24px;
  }
}
@include big-desktop{
  .contacts{
    padding-top: 20px;
  }
  .contacts__container{
    flex-direction: column-reverse;
    align-items: center;
  }
  .contacts__left{
    padding: 20px 10px;
    height: 330px;
  }
}
@include mobile{
  .contacts__input {
    max-width: 280px;
    padding: 0px 20px 0px 60px;
  }

  .contacts__link{
    width: 280px;
  }
  .contacts__map{
    width: 300px;
    height: 350px;
  }
}
