.line{

  background-color: var(--gray-color);

  &__title{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 117.52%;
    text-align: center;
   padding: 100px 0px 142px 0px;
  }

  &__list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 54px;

  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 31.5px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    width: 333px;
    min-height: 447px;
    background-color: var( --light-color);
  }
  &__img{
    width: 270px;
    height: 270px;
    margin-bottom: 32px;
  }
  &__descr{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 25px;
    line-height: 117.52%;
    padding-bottom: 32px;
  }
  &__link{
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 117.52%;
    color: var(--light-color);
  }
}
@include for-desktop{
  .line__title{
    font-size: 26px;
    padding: 40px 0px;
  }
}
@include mobile{
  .line__item{
    padding: 5px;
    width: 300px;
    height: 400px;
  }
  .line__img{
    width: 250px;
    height: 250px;
  }
}
