.cost{

  background-color: var(--gray-color);

  &__container{
    position: relative;
  }

  &__decor1{
    position: absolute;
    left: 0px;
    top: 60px;
    background-image: url('../img/cost (2).png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 245px;
    height: 441px;
  }
  &__decor2{
    position: absolute;
    right:0px;
   bottom: 0;
    background-image: url('../img/cost (1).png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 245px;
    height: 587px;
  }


  &__title{
    margin: 0;
    padding: 206px 0px 156px 0px;
    text-align: center;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
  }
  &__inner{
    display: flex;
    padding: 0px 64px 0px 72px;
    gap: 64px;
    padding-bottom: 285px;
  }
  &__text{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: justify;
    padding-bottom: 32px;
  }
  &__link{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 800;
    font-size: 13px;
    line-height: 16px;
    text-transform: uppercase;
    color: var(--light-color);
    padding: 20px 30px;
  }
}
@include for-desktop{
  .cost__title{
    padding: 80px 0px 40px 0px;
  }
  .cost__inner{
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  .cost__btn{
    display: block;
    margin: 0 auto;
  }
}
@include small-tablet{
  .cost__decor1{
   top: -40px;
  }
}
@include mobile{
  .cost__decor2{
    display: none;
  }
}
