.advantages{

  background-color: var(--gray-color);

  &__title{
    margin: 0;
    padding-top: 40px;
    max-width: 995px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 35px;
    line-height: 104.52%;
    text-align: center;
    margin-bottom: 36px;
  }
  &__subtitle{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 117.52%;
    text-align: center;
    color: #000000;
    margin-bottom: 166px;
  }
  &__inner{
    display: flex;
    gap: 11px;
  }
  &__middle{
    max-width: 371px;
  }
  &__right{
    border-radius: 20px;
    background-image: url('../img/advantages(11).png');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 255px;
    height: 314px;
  }
  &__list{
    max-width: 457px;
    padding: 15px 10px;
    border-radius: 10px;
    border: 1px solid #C1C1C1;
  }
  &__item{
    max-width: 437px;
    display: flex;
    gap: 8px;
    align-items: center;
    padding: 16px 0px;

    &-img{
      border-radius: 100%;
      width: 48px;
      height: 48px;
    }

    &-descr{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 600;
      font-size: 15px;
      line-height: 18px;
      color: #555555;
    }
    &:first-child{
      background: rgba(0, 175, 71, 0.3);
      border-radius: 10px;
    }

    &:not(:last-child){
      border-bottom: 1px dashed #C1C1C1;
    }
  }
  &__text{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 104.52%;
    margin-bottom: 24px;
  }
  &__descr{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 119.02%;
    margin-bottom: 24px;
  }
  &__link{
    color: var(--light-color);
    padding: 20px 40px;
  }
}

@include for-desktop{
  .advantages__title{
    font-size: 26px;
  }
  .advantages__inner{
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }
  .advantages__subtitle{
    margin-bottom: 40px;
  }
  .advantages__right{
    display: none;
  }
  .advantages__text{
    text-align: center;
  }
  .advantages__btn{
   margin: 0 auto;
   display: flex;
   justify-content: center;
   align-items: center;
  }
}
@include tablet{
  .advantages__title{
    font-size: 26px !important;
  }
}
@include small-tablet{
  .advantages__title{
    font-size: 18px;
    font-weight: 700;
  }
  .advantages__subtitle{
    font-size: 18px;
  }
  .advantages__item-descr{
    font-size: 14px;
  }
}
