.header{
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding-top: 20px;
  background-color: var(--gray-color);
  color: var(--text-color);

  &__container{
    position: relative;
    justify-content: center;
    display: flex;
    align-items: center;
    height: 71px;
  }
  &__logo{
    margin-right: 80px;
  }
  &__descr{
    margin: 0;
    margin-right: 80px;
    max-width: 202px;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
    color: var(--text-color);
  }
  &__location{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 164px;
    margin-right: 80px;

  &::before{
    position: absolute;
    left: -40px;
    top: -10px;
    content: url('../img/location.png');
    margin-right: 15px;
    width: 28px;
    height: 28px;
    cursor: pointer;
  }

  a{
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    text-decoration-line: underline;
    color: var(--green-color);
  }
  }
  &__location-text{
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 13px;
    line-height: 15px;
  }
  &__header__location-text{
    padding-left: 30px;
  }
  &__sotial{
  margin-right: 82px;
  }
  &__contacts{
    position: relative;
  }
  &__contacts-text{
    position: relative;
    margin: 0;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;

  &::before{
    position: absolute;
    content: '';
    border-radius: 100%;
    left: -6px;
    top: 4px;
    width: 3px;
    height: 3px;
    background: #00AF47;
  }
  }
  &__tel{
    font-family: 'TT Norms';
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    color: inherit;
  }
  &__sotial-text{
    position: absolute;
    left: -220px;
    top: -15px;
    margin: 0 ;
    font-family: 'TT Norms';
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    color: var(--light-grey-color);
  }
}

@include big-desktop{
  .header__logo{
    margin-right: 30px;
  }
  .header__descr{
    margin-right: 30px;
  }
  .header__location{
    margin-right: 30px;
  }
 .header__sotial{
  margin-right: 30px;
 }
 .header__sotial-text{
  left: -170px;
 }
}
@include tablet{
  .header__location{
    display: none;
  }
}
@include small-tablet{
  .header__descr{
    display: none;
  }
   .header__sotial{
    display: none;
  }
  .header__sotial-text{
    display: none;
  }
}
