.construct{
  background-color: var(--gray-color);

  &__title{
    margin: 0;
    padding: 100px 0px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
  }

  &__list{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 32px;

    &:not(:last-of-type){
      margin-bottom: 32px;
    }
  }

  &__item{
    position: relative;
    width: 347px;
    height: 277px;
    background: rgba(13, 13, 13, 0.55);
    border-radius: 20px;
    background-position: center;


    &-1{
      background-image: url('../img/constract (1).png');
    }

    &-2{
      background-image: url('../img/constract (2).png');
    }

    &-3{
      background-image: url('../img/constract (3).png');
    }
    &-4{
      background-image: url('../img/constract (4).png');
    }
    &-5{
      background-image: url('../img/constract (5).png');
    }
    &-6{
      background-image: url('../img/constract (6).png');
    }
    &-7{
      background-image: url('../img/constract (7).png');
    }
    &-8{
      background-image: url('../img/constract (8).png');
    }

  }

  &__descr{
    position: absolute;
    top: 20px;
    left: 20px;
    margin: 0;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 104.52%;
    color: var(--light-color);
    filter: none;
  }
}
@include for-desktop{
  .construct__title{
    font-size: 26px;
    padding: 40px 0px;
  }
}
@include mobile{
  .construct__title{
    font-size: 20px;
    line-height: 20px;
  }
  .construct__item{
    width: 300px;
    height: 277px;
  }
}
