.guarantee{
  background-color: var(--gray-color);

  &__container{
    position: relative;
  }
  &__title{
    margin: 0 auto;
    padding-top: 100px;
    max-width: 875px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 104.52%;
    text-align: center;
    color: #000000;
    margin-bottom: 36px;
  }
  &__inner{
    display: flex;
  }
  &__right{
    background-image: url('../img/guarantee (2).png');
    width: 473px;
    height: 759px;
  }
  &__decor{
    position: absolute;
    background-image: url('../img/guarantee (1).png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 245px;
    height: 542px;
    right: 0px;
    top: 242px;
  }

  &__item{
    position: relative;
    max-width: 438px;
    padding-left: 40px;

    &:not(:last-child){
      margin-bottom: 48px;
    }

    &::before{
      position: absolute;
      left: 0px;
      top: 30px;
      content: url('../img/svg/check.svg');
    }
  }
  &__text{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 4px;
  }
  &__descr{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
  }
  &__form-box{
    border-radius: 20px;
    margin: 0 auto;
    background-image: url('../img/guarantee.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    max-width: 1111px;
    min-height: 341px;
  }
  &__form-inner{
    display: flex;
    justify-content: space-between;
  }
  &__form-right{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__fotm-title{
    margin: 0;
    padding: 69px 0px 16px 54px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 47px;
  }
  &__form-subtitle{
    margin: 0;
    padding-left: 130px;
    position: relative;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    margin-bottom: 50px;
    &::before{
      position: absolute;
      left: 50px;
      top: 15px;
      content: '';
      width: 78px;
      height: 1px;
      background-color: #3FAC3B;
    }
  }
  &__form-text{
    margin: 0;
    padding: 69px 53px 8px 0px;
    margin-right: auto;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
  }
  &__phone{
    display: block;
    padding: 0px 53px 14px 0px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    color: var(--text-color);
  }
  &__sotial{
    margin-right: 53px;
  }
  &__form{
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 20px;
  }
  &__in{
    padding: 0px 28px;
    width: 327px;
    height: 60px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.07);
    border-radius: 100px;

    &::placeholder{
      font-family: 'Inter';
      font-style: normal;
      font-weight: 200;
      font-size: 18px;
      line-height: 22px;
    }
   }
}

@include for-desktop{
  .guarantee__decor{
    display: none;
  }
}
@include tablet{
  .guarantee__title{
    font-size: 24px;
  }
  .guarantee__inner{
    justify-content: center;
  }
  .guarantee__right{
    display: none;
  }
  .guarantee__fotm-title{
    font-size: 30px;
  }
  .guarantee__form-subtitle{
    padding-left: 60px;
    font-size: 20px;
    &::before{
     display: none;
    }
  }
}
@include small-tablet{
  .guarantee__title{
    font-size: 20px;
  }
  .guarantee__form-inner{
    flex-direction: column;
    align-items: center;
  }
  .guarantee__fotm-title{
    padding: 40px 0px 0px 0px;
    font-size: 22px;
    text-align: center;
  }
  .guarantee__form-subtitle{
    padding: 0;
    text-align: center;
    margin-bottom: 10px;
  }
  .guarantee__form-text{
    padding: 0;
    text-align: center;
  }
  .guarantee__sotial{
    margin-bottom: 10px;
  }
  .guarantee__form{
    flex-direction: column;
  }
  .guarantee__btn{
    min-width: 300px;
  }
  .guarantee__input{
    max-width: 300px;
  }
}
