.form{
  background-color: var(--gray-color);
  padding-top: 101px;

  &__inner{
    background-image: url('../img/form (1).png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 90px 61.5px;
    max-width: 1110px;
    height: 664px;
    display: flex;
    gap: 57px;
  }
  &__title{
    margin: 0;
    max-width: 513px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 127.52%;
    padding-bottom: 42px;
  }
  &__subtitle{
    position: relative;
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    padding-bottom: 42px;

    &::before{
      position: absolute;
      left: 30px;
      top: 13px;
      content: '';
      width: 87px;
      height: 2px;
      background-color: #3FAC3B;
    }
  }
  &__left{
    display: flex;
    flex-direction: column;
  }
  &__right{
    width: 393px;
    height: 319px;
  }
  &__sotial-box{
    display: flex;
    margin-bottom: 26px;
    gap: 16px;
  }
  &__text{
    margin: 0;
    max-width: 178px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 127.52%;
  }
  &__input{
    outline: none;
    padding-left: 90px;
    border-radius: 50px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    max-width: 395px;
    height: 64px;
    background: #FFFFFF;
    margin-bottom: 25px;

    &::placeholder{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #D8D8D8;
    }
  }
  &__btn{
    width: 396px;
    height: 64px;
  }
  &__link{
    width: 395px;
    color: var(--light-color);
  }
}
@include big-desktop{
  .form__subtitle{
    text-align: left;
    &::before{
     display: none;
    }
  }
}
@include small-tablet{
  .form__title{
    font-size: 20px;
    line-height: 22px;
  }
  .form__inner{
    padding: 20px 10px;
    height: 500px;
  }
  .form__right{
    display: none;
  }
  .form__sotial-box{
    flex-direction: column;
  }
  .form__input {
    max-width: 260px;
    padding: 0px 20px 0px 60px;
  }
  .form__btn{
    max-width: 260px;
  }
  .form__link{
    width: 260px;
  }
}

