.quiz__container{
  position: relative;
  border-radius: 20px;
  background: rgba(205, 205, 205, 0.3);
	width: 730px;
	min-height: 638px;
	padding: 30px 60px;
	font-family: 'Roboto', sans-serif;

}
.quiz__bredcrumps{
  position: absolute;
  top: 140px;
  left: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quiz__bredcramps-item{
  width: 74px;
  height: 12px;
  background: #969696;
  border-radius: 100px;

  &:first-child{
    background-color: #3FAC3B;
  }
  &:not(:last-child){
    margin-right: 20px;
  }
}
.quiz__img-box{
  display: flex;
  justify-content: center;
  gap: 48px;
  margin-bottom: 48px;
}

.quiz__questions {
  padding-top: 64px;
  text-align: center;
  font-family: 'TT Norms';
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  margin-bottom: 94px;
}

.quiz-question__title {
  font-family: 'TT Norms';
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
}

.quiz-question__answers {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.quiz-question__label {
	margin-bottom: 10px;
}

.quiz-question__answers input[type="text"] {
	width: 200px;
	height: 40px;
	padding-left: 10px;
}

.quiz-question__btn {
	color: #fff;
  width: 120px;
	background-color:  #3FAC3B;
	border: none;
	padding: 10px 15px;
	cursor: pointer;
}

.error {
	outline: 3px solid red !important;
}
@include big-desktop{
  .quiz__container{
    width: 350px;
    height: 628px;
    // width: 280px;
    // height: 628px;
  }
  .quiz__bredcrumps{
    display: none;
  }
  .quiz__img{
    display: none;
  }
}
@include mobile{
  .quiz__container{
    width: 290px;
    height: 428px;
    padding: 15px;
  }
}
