.additional{
  background-color: var(--gray-color);

  &__title{
    margin: 0;
    padding: 100px 0px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
    text-align: center;
  }

  &__list{
    max-width: 910px;
    display: flex;
    gap: 30px;
    margin-bottom: 30px;
  }
  &__item{
    position: relative;
  }
  &__item-inner{
    border-radius: 10px;
    background-image: url('../img/additional (1).png');
    width: 540px;
    height: 315px;
    filter: brightness(0.5);

    &-2{
      background-image: url('../img/additional (5).png');
    }
  }
  &__item-title{
    position: absolute;
    top: 156px;
    left: 18px;
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 117.52%;
    color: var(--light-color);
  }
  &__descr{
    position: absolute;
    top: 207px;
    left: 18px;
    margin: 0;
    max-width: 478px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 117.52%;
    color: var(--light-color);
  }
  &__list2{
    max-width: 910px;
    display: flex;
    gap: 30px;
  }
  &__item2{
    position: relative;
  }
  &__item2-inner{
    border-radius: 10px;
    background-image: url('../img/additional (2).png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 350px;
    height: 248px;
    filter: brightness(0.5);

    &-2{
      background-image: url('../img/additional (3).png');
    }

    &-3{
      background-image: url('../img/additional (4).png');
    }
  }
  &__item2-title{
    position: absolute;
    top: 43px;
    left: 11px;
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 117.52%;
    color: var(--light-color);

    &-2{
      position: absolute;
      top: 77px;
    }
  }
  &__descr2{
    position: absolute;
    top: 125px;
    left: 11px;
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 117.52%;
    color: var(--light-color);
  }
}
@include big-desktop{
  .additional__list{
    max-width: none;
    flex-direction: column;
    align-items: center;
  }
  .additional__list2{
    max-width: none;
    flex-direction: column;
    align-items: center;
  }
  .additional__item2-inner{
    width: 540px;
    height: 315px;
  }
}
@include for-desktop{
  .additional__title{
    font-size: 26px;
    padding: 40px 0px;
  }
}
@include mobile{
  .additional__title{
    font-size: 20px;
    line-height: 20px;
  }
  .additional__item-inner{
    width: 300px;
    height: 248px;
  }
  .additional__item-title{
    top: 30px;
  }
  .additional__descr{
    top: 120px;
  }
  .additional__item2-inner{
    width: 300px;
    height: 248px;
  }
}
