.proect{
   background-color: var(--gray-color);

   &__container{
    position: relative;
    padding-bottom: 300px;
   }
   &__decor{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 245px;
    height: 542px;
    background-image: url('../img/cost (2).png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
   }

   &__title{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 117.52%;
    text-align: center;
    padding-bottom: 9px;
   }
   &__subtitle{
    margin: 0;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 117.52%;
    text-align: center;
    padding-bottom: 120px;
   }

   &__inner{
    display: flex;
    gap: 27px;
   }
   &__img{
    width: 379px;
    height: 413px;
    border-radius: 20px;
   }
   &__item-title{
    max-width: 323px;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 117.52%;
    padding-bottom: 16px;
   }
   &__item{
    display: flex;
    align-items: center;
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 127.52%;
    padding-bottom: 16px;

    &::before{
      content: url('../img/svg/circle.svg');
      width: 10px;
      height: 10px;
      border-radius: 100%;
     padding: 0px 20px 30px 0px;
    }
   }
}
@include for-desktop{
  .proect__container{
    padding-bottom: 40px;
  }
  .proect__title{
    padding: 40px 0px;
    font-size: 26px;
  }
  .proect__subtitle{
    display: none;
  }
  .proect__inner{
    margin: 0 auto;
    flex-direction: column;
    max-width: 500px;
    gap: 40px;
  }
  .proect__decor{
    bottom: -300px;
  }
  .proect__item-title{
    text-align: center;
  }
}
@include mobile{
  .proect__decor{
    display: none;
  }
}
